.frontis-dynamic-content {
	.components-button {
		svg {
			fill: #fff;
		}
	}
}

// Popover style
.frontis-dynamic-content-popover {
	width: 300px;
	.frontis-dynamic-content-fields {
		.frontis-dynamic-content-text-control {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-bottom: 15px;

			 .components-base-control__field {
				max-width: 100px;
				margin-bottom: 0 !important;
			}

			.components-base-control {
				&.components-toggle-control {
					margin-bottom: 0 !important;
				}
			}
		}
	}
}

.frontis-dynamic-content-advance {
	padding: 0;
	margin-top: 30px !important;
	border: none;
	.components-panel__body-title {
		background: #f0f0f0;
	}

	.components-base-control__field {
		flex-direction: row !important;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 15px;

		.components-text-control__input {
			width: 130px;
		}
	}
}
